<template>
	<div class="headers">
		<div id="bbtj">
			<div class="titBox">
				<a href="javascipt:;" v-for="(item,index) in jinr" @click="jinrsd(index)" :class="{'current':index == jinrs}">{{item}}</a>
				<b style="color:red;">【點擊獲取相應時間統計數據】</b>
			</div>
			<div class="cont bbtjMsg" id="cont">
				<table id="ajax_count">
					<thead class="sportFirst">
						<tr>
							<th>項目</th>
							<th>注單量</th>
							<th>下注總額</th>
							<th>有效下注總額</th>
							<th>盈利總額</th>
						</tr>
					</thead>
					<tbody>
						<tr>
							<td>818彩票</td>
							<td style="text-align:center;">0</td>
							<td style="text-align:center;">0.00</td>
							<td style="text-align:center;">0.00</td>
							<td style="text-align:center;">0.00</td>
						</tr>
					</tbody>
				</table>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				jinr:['今日','昨日','本周','上周'],
				jinrs:0
			}
		},
		methods: {
			jinrsd(star){
				let that = this;
				that.jinrs = star;
			}
		},
		mounted: function() {

		}
	}
</script>
<style rel="stylesheet" type="text/css" src="../../style/grzx.css" scoped="scoped" /></style>


